.block_options {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin: 14px 26px;
  min-width: 400px;
}
.block_options .options_window {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.block_options .options_window .option {
  transition: 0.3s;
  font-size: 14px;
  color: #4d4d4d;
  background: #f0edff;
  border-radius: 31px;
  padding: 8px 16px;
  margin: 2.5px 3px;
  cursor: pointer;
  display: flex;
  flex-grow: 0;
  white-space: nowrap;
}
.block_options .options_window .option:hover {
  transition: 0.3s;
  background: #d9d6e7;
}
.block_options .options_window .option.active {
  background: #9173d0;
  color: #ffffff;
  font-weight: bold;
  transition: 0.3s;
}
.block_options .options_window .option.active:hover {
  background: #685297;
  transition: 0.3s;
}
.block_options .options_window .option.visited {
  color: #b7aad4;
  transition: 0.3s;
}
.block_options .options_window .option.visited:hover {
  transition: 0.3s;
  background: #d9d6e7;
  color: #4d4d4d;
}
.block_options .options_window .go_to_checklist,
.block_options .options_window .button_check {
  cursor: pointer;
  transition: 0.3s;
  flex-grow: 1;
  flex-shrink: 0;
  height: 22px;
  padding: 12px 25px;
  border-radius: 31px;
  font-weight: bold;
  line-height: 21px;
  font-size: 14px;
  text-transform: uppercase;
  color: #ffffff;
}
.block_options .options_window .go_to_checklist:hover,
.block_options .options_window .button_check:hover {
  transition: 0.3s;
}
.block_options .options_window .button_check {
  background: #81ae24;
}
.block_options .options_window .button_check:hover {
  transition: 0.3s;
  background: #618319;
}
.block_options .options_window .go_to_checklist {
  background: #f7712d;
}
.block_options .options_window .go_to_checklist:hover {
  transition: 0.3s;
  background: #be5a28;
}
@media (max-width: 755px) {
  .block_options .options_window .option {
    font-size: 12px;
  }
}
