.block_messages {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100%;
  flex: 1;
  flex-shrink: 0;
}
.block_messages ::-webkit-scrollbar {
  display: none;
}
.block_messages .message_window {
  scroll-behavior: smooth;
  overflow-y: auto;
  width: 68%;
  display: flex;
  flex-direction: column;
  align-items: center;
  -ms-overflow-style: none;
}
.block_messages .message_window:after {
  position: absolute;
  left: 0;
  bottom: -34px;
  right: 0;
  height: 87px;
  width: 100%;
  box-shadow: inset 0 80px 60px -60px rgba(104, 58, 58, 0), inset 0 -147px 18px -101px #ffffff;
}
@keyframes smooth-appearance {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.block_messages .message_window .message {
  padding: 13px 26px;
  width: 70%;
  margin: 5px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  animation: smooth-appearance 0.3s ease;
}
.block_messages .message_window .message .text_message {
  flex: 1;
  font-size: 16px;
  line-height: 24px;
}
.block_messages .message_window .message .text_message h3 {
  font-weight: normal;
  font-size: 16px;
}
.block_messages .message_window .incoming {
  color: #4d4d4d;
  position: relative;
  background: #f0f0f0;
  border-radius: 14px 14px 14px 0px;
  margin-right: 50px;
}
.block_messages .message_window .incoming:before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: -11px;
  width: 12px;
  height: 10px;
  background: url("../assets/images/edge_gray.svg") no-repeat;
}
.block_messages .message_window .outgoing {
  color: #4d4d4d;
  position: relative;
  background: #f0edff;
  border-radius: 14px 14px 0px 14px;
  margin-left: 50px;
}
.block_messages .message_window .outgoing:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  right: -11px;
  width: 12px;
  height: 10px;
  background: url("../assets/images/edge_purpure.svg") no-repeat;
}
.block_messages .message_window .incoming_final {
  color: #4d4d4d;
  position: relative;
  background: #daf2a9;
  border-radius: 14px 14px 14px 0px;
  margin-right: 50px;
}
.block_messages .message_window .incoming_final:before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: -11px;
  width: 12px;
  height: 10px;
  background: url("../assets/images/edge_green.svg") no-repeat;
}
@media (max-width: 755px) {
  .block_messages .message_window .message .text_message {
    font-size: 13px;
    line-height: 24px;
  }
  .block_messages .message_window .message .text_message h3 {
    font-size: 13px;
  }
}
