.line_progress .line_block {
  display: flex;
  justify-content: center;
  align-items: center;
}
.line_progress .line_block .circle {
  transition: all 0.3s;
  flex-shrink: 0;
  position: relative;
  width: 11px;
  height: 11px;
  border-radius: 50px;
  background: #e4b813;
  margin: 2px;
}
.line_progress .line_block .line {
  transition: all 0.3s;
  flex-shrink: 0;
  height: 3px;
  width: 62px;
  background: #e4b813;
}
.line_progress .line_block .yellow {
  background: #fabd1d;
}
.line_progress .line_block .white {
  background: #ffffff;
}
.line_progress .line_block .purpure {
  background: #9173d0;
}
.line_progress .line_block .gray {
  background: #cbcbcb;
}
.line_progress .line_block .last {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15px;
  height: 15px;
  background: #ffffff;
}
.line_progress .line_block .last:after {
  content: "";
  display: block;
  background: url("../assets/images/check_img.svg") no-repeat;
  width: 9px;
  height: 7px;
}
.line_progress .line_block .last.green {
  background: #81ae24;
}
.line_progress .line_block .last.green:after {
  content: "";
  display: block;
  background: url("../assets/images/check_img_white.svg") no-repeat;
  width: 9px;
  height: 7px;
}
.line_progress .line_block .label {
  position: absolute;
  left: 0;
  font-size: 14px;
}
.line_progress .line_block .label.start {
  top: -3px;
  left: -51px;
  color: #5a5a5a;
  font-weight: bold;
}
.line_progress .line_block .label.run_check {
  font-weight: bold;
  top: -8px;
  text-align: left;
  left: 23px;
  color: #5a5a5a;
}
@media (max-width: 755px) {
  .line_progress .line_block .circle {
    transition: all 0.3s;
    width: 9px;
    height: 9px;
  }
  .line_progress .line_block .line {
    transition: all 0.3s;
    width: 40px;
  }
  .line_progress .line_block .last {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 13px;
    height: 13px;
  }
  .line_progress .line_block .last:after {
    content: "";
    display: block;
    background: url("../assets/images/check_img.svg") no-repeat;
    width: 7px;
    height: 5px;
  }
}
